<template>
  <div class="auth">
    <h1>Register</h1>
    <ul id="form">
      <li><input v-model="username" placeholder="Username" /></li>
      <li><input v-model="password1" placeholder="Password" /></li>
      <li><input v-model="password2" placeholder="Repeat password" /></li>
      <li><button @click="regUser">Register</button></li>
    </ul>
    <ModalRegister v-bind:msg="message" />
  </div>
</template>

<script>
// @ is an alias to /src
import ModalRegister from "@/components/ModalRegister.vue";
import { REGISTER, USERNAME_EXISTS } from "./../constants/graphql.js";

export default {
  name: "Auth",
  components: {
    ModalRegister
  },
  data() {
    return {
      username: "",
      password1: "",
      password2: "",
      message: ""
    };
  },
  apollo: {},
  methods: {
    clearFields() {
      this.username = "";
      this.password1 = "";
      this.password2 = "";
    },
    async regUser() {
      if (
        this.password1 === this.password2 &&
        this.password1 !== "" &&
        this.username !== ""
      ) {
        const exist = await this.$apollo.query({
          query: USERNAME_EXISTS,
          variables: {
            username: this.username
          }
        });
        if (Boolean(exist.data.usernameExists) === false) {
          const result = await this.$apollo.query({
            query: REGISTER,
            variables: {
              username: this.username,
              password: this.password1
            }
          });
          console.log(result.data.register.id);
          const Userid = result.data.register.id;
          const Username = result.data.register.username;
          this.message = `User with username "${Username}" has been registered successfully! Id: ${Userid}`;
          this.$modal.show("notif");
          this.clearFields();
        } else {
          this.message = `User with username "${this.username}" already exists. Try new username.`;
          this.$modal.show("notif");
          this.clearFields();
        }
      } else {
        if (this.password1 !== this.password2) {
          this.message = "Two passwords don't match. Try again.";
          this.$modal.show("notif");
          this.clearFields();
        } else if (this.password1 === "" || this.username === "") {
          this.message = "All fields are required";
          this.$modal.show("notif");
          this.clearFields();
        }
      }
    }
  }
};
</script>

<style>
#form {
  list-style-type: none;
}
.vm--modal {
  height: max-content;
  width: max-content;
}
</style>
